*:where(:not(iframe, canvas, img, svg, video):not(svg *)) {
  all: unset;
  display: revert;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
button {
  cursor: pointer;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

table {
  border-collapse: collapse;
}

textarea {
  white-space: revert;
}

html {
  font-size: 100%;
}

body {
  font-family: "montserrat";
  font-weight: 500;
  font-size: 1rem;
  color: #1A1919;
  background-color: #F9F9F9;
}

.container {
  max-width: 1222px;
  padding: 0px 11px;
  margin: 0 auto;
}

.hashtag {
  font-family: "montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 25px;
  line-height: 30px;
  color: #FFFFFE;
  margin-bottom: 20px;
}

.h1 {
  font-family: "podkova";
  font-style: normal;
  font-weight: 600;
  font-size: 80px;
  line-height: 80px;
  color: #FFFFFE;
}

.big-content {
  max-width: 1760px;
  margin: 0 auto;
}

.white-links {
  font-family: "montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 17px;
  line-height: 30px;
  /* identical to box height, or 176% */
  text-decoration-line: underline;
  color: #FFFFFF;
}

.main-screen {
  display: flex;
  background-color: #008EC1;
  color: #fff;
}
.main-screen .illustration-part {
  width: 1041px;
}
.main-screen .illustration-part img {
  width: 1041px;
}
.main-screen .back-block {
  margin: 0 auto;
  width: 730px;
  padding-left: 32px;
  padding-top: 47px;
  padding-bottom: 106px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.main-screen .logo-part {
  display: flex;
  align-items: baseline;
}
.main-screen .logo-part a {
  display: block;
}
.main-screen .logo-part a.logo-nafto {
  margin-right: 75px;
}
.main-screen .header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.navigation {
  padding-top: 10px;
  padding-left: 0px;
}
.navigation-ul {
  display: flex;
  flex-direction: column;
}
.navigation-li {
  margin-bottom: 16px;
}
.navigation-li a {
  transition: all ease 0.3s;
}
.navigation-li a:hover {
  color: #1a1919;
}

.mobile-main-illustration {
  display: none;
}

.project-idea {
  padding-top: 140px;
  padding-bottom: 80px;
}

.h2 {
  font-family: "podkova";
  font-style: normal;
  font-weight: 600;
  font-size: 50px;
  line-height: 48px;
  margin-bottom: 15px;
  color: #1A1919;
}

.section-subname, .normal_text {
  font-family: "montserratmedium";
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 27px;
  color: #1A1919;
}

.section-subname {
  margin-bottom: 70px;
}

.normal_text {
  margin-bottom: 20px;
}
.normal_text span {
  font-family: "montserrat";
}

.wrap-parts {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  gap: 100px 45px;
  grid-template-areas: "part-1 part-1 part-2 part-2" "part-1 part-1 part-2 part-2" "part-1 part-1 part-4 part-4" "part-3 part-3 part-4 part-4" "part-3 part-3 part-4 part-4" "part-3 part-3 part-5 part-5";
}

.part-1 {
  grid-area: part-1;
  background-image: url("../../img/illustrations/women.png");
  background-position: 25%;
  background-repeat: no-repeat;
}

.part-2 {
  grid-area: part-2;
  padding-left: 60px;
}

.part-3 {
  grid-area: part-3;
}

.part-4 {
  grid-area: part-4;
  background-image: url("../../img/illustrations/men.png");
  background-size: cover;
  background-position: 75%;
  background-repeat: no-repeat;
  margin-left: 60px;
}

.part-img {
  border-radius: 15px;
  overflow: hidden;
}
.part-img img {
  width: 100%;
  border-radius: 15px;
}

.h3 {
  font-family: "podkova";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 22px;
  color: #008EC1;
  margin-bottom: 30px;
}

.story-section {
  padding-top: 0px;
  padding-bottom: 80px;
}
.story-section .h2 {
  margin-bottom: 85px;
}

.card {
  border-radius: 15px;
  overflow: hidden;
  width: 258px;
  height: 432px;
  display: block;
  position: relative;
}
.card:hover img {
  transform: scale(1.1);
}
.card img {
  transition: all ease 0.4s;
  display: block;
  object-fit: cover;
  height: 100%;
}
.card .hero-info {
  position: absolute;
  left: 0px;
  bottom: 0px;
  padding: 25px;
}
.card .hero-name {
  font-family: "montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #FFFFFF;
  display: block;
}
.card .hero-work {
  font-family: "montserratregular";
  font-style: normal;
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  color: #FFFFFF;
  padding-top: 10px;
}

.wrap-cards {
  display: flex;
  justify-content: flex-start;
  flex-flow: wrap;
}
.wrap-cards .card:not(:nth-child(4n)) {
  margin-right: 49px;
  margin-bottom: 50px;
}

