.l-footer {
  margin: 30px;
  background: #F4F4F4;
  border-radius: 30px;
  padding: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.l-footer .logos-part {
  display: flex;
  align-items: center;
}
.l-footer .nafto {
  margin-right: 53px;
}
.l-footer .menu {
  display: flex;
}
.l-footer .menu li:not(:last-child) {
  margin-right: 30px;
}
.l-footer .menu a {
  font-family: "montserratmedium";
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 30px;
  text-decoration-line: underline;
  color: #1A1919;
  transition: all ease 0.3s;
}
.l-footer .menu a:hover {
  color: #008ec1;
}
.l-footer .socials {
  display: flex;
  align-items: center;
}
.l-footer .socials li:not(:last-child) {
  margin-right: 30px;
}
.l-footer .socials a svg path {
  transition: all ease 0.3s;
}
.l-footer .socials a:hover svg path {
  fill: #008ec1;
}

